"use client";

import { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { format } from "date-fns";
import { Plus, Trash2, Info, AlertCircle, CheckCircle2, Loader2 } from "lucide-react"; // Added Loader2
import { useContract } from "@/context/contract-context";
import { toast } from "sonner";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Badge } from "@/components/ui/badge";

export default function CreateVoteForm() {
  const { contract } = useContract();

  const [formData, setFormData] = useState({
    description: "",
    restriction: "0", // OpenToAll (default)
    resultType: "0", // RankBased (default)
    startTime: "",
    endTime: "",
    maxVoters: "0", // Default for OpenToAll and RegistrationRequired (set to 0 means unlimited for RegistrationRequired)
    proposals: ["", ""] // minimum 2 proposals required
  });

  const [errors, setErrors] = useState({
    description: "",
    startTime: "",
    endTime: "",
    maxVoters: "",
    proposals: ""
  });

  const [validations, setValidations] = useState({
    description: false,
    startTime: false,
    endTime: false,
    proposals: false,
    maxVoters: true // Default true (no error initially)
  });

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [currentStep, setCurrentStep] = useState(1);

  // Validate form when data changes
  useEffect(() => {
    validateForm();
  }, [formData]);

  const validateForm = () => {
    const newErrors = { description: "", startTime: "", endTime: "", maxVoters: "", proposals: "" };
    const newValidations = { description: false, startTime: false, endTime: false, proposals: false, maxVoters: true };

    // Description validation
    if (formData.description.trim().length < 10) {
      newErrors.description = "Description should be at least 10 characters long";
    } else {
      newValidations.description = true;
    }

    // Time validation
    const now = new Date();
    const minStartTime = new Date(now.getTime() + 3 * 60 * 1000); // 3 minutes from now
    const startDate = formData.startTime ? new Date(formData.startTime) : null;
    const endDate = formData.endTime ? new Date(formData.endTime) : null;

    if (!startDate) {
      newErrors.startTime = "Start time is required";
    } else if (startDate < minStartTime) {
      newErrors.startTime = "Start time must be at least 3 minutes from now";
    } else {
      newValidations.startTime = true;
    }

    if (!endDate) {
      newErrors.endTime = "End time is required";
    } else if (startDate && endDate <= startDate) {
      newErrors.endTime = "End time must be after start time";
    } else {
      newValidations.endTime = true;
    }

    // Max voters validation for limited/registration voting
    const maxVoters = parseInt(formData.maxVoters);
    if (formData.restriction === "1") { // Limited Number
      if (isNaN(maxVoters) || maxVoters < 1) {
        newErrors.maxVoters = "Maximum voters must be at least 1 for limited voting";
        newValidations.maxVoters = false;
      }
    } else if (formData.restriction === "2") { // Registration Required
      if (isNaN(maxVoters) || maxVoters < 0) {
        newErrors.maxVoters = "Maximum voters must be 0 (unlimited) or a positive number";
        newValidations.maxVoters = false;
      } else if (maxVoters === 0) {
        // 0 is valid for registration required (means unlimited)
        newValidations.maxVoters = true;
      }
    }
    // For OpenToAll (0), maxVoters field is irrelevant for validation but should be sent as 0

    // Proposal validation
    const validProposals = formData.proposals.filter(p => p.trim()).length;
    if (formData.proposals.length < 2 || validProposals < 2) {
      newErrors.proposals = "At least 2 non-empty proposals are required";
    } else {
      newValidations.proposals = true;
    }

    setErrors(newErrors);
    setValidations(newValidations);
  };

  const isFormValid = () => {
    // Trigger validation one last time before checking
    validateForm();
    return Object.values(validations).every(value => value === true);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSelectChange = (name, value) => {
    // Reset maxVoters logic when changing restriction type
    if (name === 'restriction') {
      setFormData((prev) => ({
        ...prev,
        [name]: value,
        maxVoters: "0" // Reset to default when changing type
      }));
    } else {
      setFormData((prev) => ({ ...prev, [name]: value }));
    }
  };

  const handleProposalChange = (index, value) => {
    const updatedProposals = [...formData.proposals];
    updatedProposals[index] = value;
    setFormData((prev) => ({ ...prev, proposals: updatedProposals }));
  };

  const addProposal = () => {
    setFormData((prev) => ({
      ...prev,
      proposals: [...prev.proposals, ""]
    }));
  };

  const removeProposal = (index) => {
    if (formData.proposals.length <= 2) {
        toast.warning("You need at least 2 proposals.");
        return; // Minimum 2 proposals required
    }
    const updatedProposals = [...formData.proposals];
    updatedProposals.splice(index, 1);
    setFormData((prev) => ({ ...prev, proposals: updatedProposals }));
  };

  const nextStep = () => {
    if (currentStep === 1 && validations.description) {
      setCurrentStep(2);
    } else if (currentStep === 2 && validations.maxVoters) {
      setCurrentStep(3);
    } else if (currentStep === 3 && validations.startTime && validations.endTime) {
      setCurrentStep(4);
    } else {
        // If validation fails, highlight errors for the current step
        validateForm();
    }
  };

  const prevStep = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Final validation check
    validateForm(); // Ensure errors/validations are up-to-date
    if (!isFormValid()) {
      toast.error("Please fix all validation errors before submitting", {
        description: "Check each step for red error messages or missing green checkmarks.",
      });
      // Optionally, navigate back to the first step with an error
      if (!validations.description) setCurrentStep(1);
      else if (!validations.maxVoters) setCurrentStep(2);
      else if (!validations.startTime || !validations.endTime) setCurrentStep(3);
      else if (!validations.proposals) setCurrentStep(4);
      return;
    }

    if (!contract) {
        toast.error("Contract not loaded. Please ensure your wallet is connected and the page is fully loaded.");
        return;
    }

    setIsSubmitting(true);
    const toastId = toast.loading("Submitting campaign to the blockchain...");

    try {
      // Convert times to Unix timestamps (seconds)
      const startTimestamp = Math.floor(new Date(formData.startTime).getTime() / 1000);
      const endTimestamp = Math.floor(new Date(formData.endTime).getTime() / 1000);

      // Convert numeric string values to actual numbers
      const restrictionValue = parseInt(formData.restriction);
      const resultTypeValue = parseInt(formData.resultType);
      // Use 0 if restriction is OpenToAll or if RegistrationRequired has maxVoters set to 0 (unlimited)
      const maxVotersValue = (restrictionValue === 0 || (restrictionValue === 2 && formData.maxVoters === "0"))
                             ? 0
                             : parseInt(formData.maxVoters);

      // Filter out empty proposals
      const filteredProposals = formData.proposals.filter(p => p.trim());

      console.log("Sending to contract:", {
        description: formData.description.trim(),
        restriction: restrictionValue,
        resultType: resultTypeValue,
        startTime: startTimestamp,
        endTime: endTimestamp,
        maxVoters: maxVotersValue,
        proposals: filteredProposals
      });

      // Call the contract method
      const tx = await contract.createProposalBasedCampaign(
        formData.description.trim(),
        restrictionValue,
        resultTypeValue,
        startTimestamp,
        endTimestamp,
        maxVotersValue,
        filteredProposals
      );

      toast.loading("Waiting for transaction confirmation...", { id: toastId });

      // Wait for transaction to be mined
      const receipt = await tx.wait();

      toast.success("Campaign created successfully!", {
          id: toastId,
          description: `Transaction Hash: ${receipt.transactionHash.substring(0, 10)}...`, // Show partial hash
      });

      // Reset form
      setFormData({
        description: "",
        restriction: "0",
        resultType: "0",
        startTime: "",
        endTime: "",
        maxVoters: "0",
        proposals: ["", ""]
      });
      setErrors({ description: "", startTime: "", endTime: "", maxVoters: "", proposals: "" });
      setValidations({ description: false, startTime: false, endTime: false, proposals: false, maxVoters: true });
      setCurrentStep(1);

    } catch (error) {
      console.error("Error creating campaign:", error);
      // Attempt to parse more specific errors if available (e.g., from Ethers)
      let errorMessage = "Error creating campaign.";
      if (error.reason) {
          errorMessage = `Transaction failed: ${error.reason}`;
      } else if (error.message) {
          // Try to extract a useful message
          if (error.message.includes("user rejected transaction")) {
              errorMessage = "Transaction rejected by user.";
          } else if (error.message.includes("insufficient funds")) {
              errorMessage = "Insufficient funds for transaction.";
          }
          // Add more specific checks if needed based on common errors
      }
      toast.error(errorMessage, {
          id: toastId,
          description: "Please check console logs for details and try again.",
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  // Calculate minimum start time (current time + 3 minutes as per contract logic)
  const calculateMinStartTime = () => {
    const minStart = new Date();
    minStart.setMinutes(minStart.getMinutes() + 3);
    return format(minStart, "yyyy-MM-dd'T'HH:mm");
  };

  // Calculate minimum end time based on start time (must be at least 1 minute after start time)
  const calculateMinEndTime = (startTime) => {
    if (!startTime) return calculateMinStartTime(); // If no start time, min end time is same as min start time
    const start = new Date(startTime);
    start.setMinutes(start.getMinutes() + 1); // End must be at least 1 min after start
    return format(start, "yyyy-MM-dd'T'HH:mm");
  };

  const minStartTimeStr = calculateMinStartTime();
  const minEndTimeStr = calculateMinEndTime(formData.startTime);


  const fadeIn = {
    initial: { opacity: 0, y: 20 },
    animate: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 }
  };

  return (
    <motion.div
      className="py-8 px-4 sm:px-6 md:px-8 bg-gradient-to-b from-background/10 via-background/20 to-background/30 min-h-[80vh] rounded-lg backdrop-blur-sm"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="max-w-3xl mx-auto">
        {/* Token information alert */}
        <Alert className="mb-8 border border-amber-200 bg-amber-50 dark:bg-amber-950/20 dark:border-amber-900 shadow-sm">
          <Info className="h-5 w-5 text-amber-600 dark:text-amber-400 flex-shrink-0 mt-0.5" />
          <AlertDescription className="text-amber-800 dark:text-amber-300 ml-2">
            <p className="font-medium">Ensure sufficient MATIC for gas fees.</p>
            <p className="text-sm mt-1">
              Voting campaign creation requires a transaction on the Polygon network, which consumes MATIC for gas. Obtain test MATIC from the{" "}
              <a href="https://faucet.polygon.technology/" target="_blank" rel="noopener noreferrer" className="underline font-semibold hover:text-amber-700 dark:hover:text-amber-200 transition-colors">
                Polygon Faucet
              </a>{" "}
              if needed.
            </p>
          </AlertDescription>
        </Alert>

        {/* Step indicators */}
        <div className="mb-10">
          <div className="flex justify-between items-start"> {/* Changed items-center to items-start */}
            {[
              { step: 1, title: "Details" },
              { step: 2, title: "Configuration" },
              { step: 3, title: "Time Frame" },
              { step: 4, title: "Proposals" }
            ].map(({ step, title }, index, arr) => (
              <motion.div
                key={step}
                className={`relative flex flex-col items-center ${index < arr.length - 1 ? "flex-1" : ""}`}
                animate={{ scale: currentStep === step ? 1.05 : 1 }}
                transition={{ type: "spring", stiffness: 300, damping: 15 }}
              >
                <div
                  className={`w-10 h-10 rounded-full flex items-center justify-center border-2 transition-all duration-300 ${
                    currentStep === step
                      ? "bg-primary text-primary-foreground border-primary scale-110 shadow-md"
                      : currentStep > step
                        ? "bg-primary/80 text-primary-foreground border-primary/80"
                        : "bg-muted text-muted-foreground border-muted-foreground/30"
                  }`}
                >
                  {currentStep > step ? <CheckCircle2 className="h-5 w-5" /> : step}
                </div>
                <div className={`text-xs mt-2 text-center font-medium transition-colors duration-300 ${currentStep >= step ? 'text-foreground' : 'text-muted-foreground'}`}>
                  {title}
                </div>
                {/* Connector Line */}
                {index < arr.length - 1 && (
                   <div className="absolute top-5 left-1/2 w-full h-0.5" style={{ transform: 'translateX(calc(1.25rem))', zIndex: -1 }}> {/* Adjusted positioning */}
                    <div className={`h-full w-full ${currentStep > step ? "bg-primary/80" : "bg-muted-foreground/30"} transition-colors duration-300`}></div>
                   </div>
                )}
              </motion.div>
            ))}
          </div>
        </div>

        <form onSubmit={handleSubmit} className="space-y-8">
          <AnimatePresence mode="wait">
            {/* Step 1: Details */}
            {currentStep === 1 && (
              <motion.div key="step1" {...fadeIn} transition={{ duration: 0.3 }}>
                <Card className="border border-border/50 shadow-sm overflow-hidden">
                  <CardHeader className="space-y-1 bg-muted/30 border-b border-border/50">
                    <Badge variant="secondary" className="w-fit mb-2">Step 1 of 4</Badge>
                    <CardTitle className="text-2xl font-semibold">Campaign Details</CardTitle>
                    <CardDescription>
                      Provide a clear and detailed description for your voting campaign.
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="p-6 space-y-4">
                    <div className="space-y-2">
                      <div className="flex justify-between items-center">
                        <Label htmlFor="description" className="text-sm font-medium">
                          Campaign Description
                        </Label>
                        {validations.description && !errors.description && (
                          <motion.span
                             initial={{ opacity: 0, scale: 0.5 }} animate={{ opacity: 1, scale: 1 }}
                             className="text-xs text-green-600 dark:text-green-400 flex items-center">
                            <CheckCircle2 className="h-3 w-3 mr-1" /> Valid
                          </motion.span>
                        )}
                      </div>
                      <Textarea
                        id="description"
                        name="description"
                        placeholder="Describe what this campaign is about in detail. What question are voters deciding on? Minimum 10 characters."
                        value={formData.description}
                        onChange={handleChange}
                        required
                        className={`min-h-32 transition-all duration-150 ${
                          errors.description ? "border-red-500 focus-visible:ring-red-500/50 shadow-inner shadow-red-500/10" :
                          validations.description ? "border-green-500 focus-visible:ring-green-500/50 shadow-inner shadow-green-500/10" : "focus-visible:ring-primary/50"
                        }`}
                      />
                      <AnimatePresence>
                        {errors.description && (
                          <motion.p
                            initial={{ opacity: 0, height: 0 }}
                            animate={{ opacity: 1, height: "auto" }}
                            exit={{ opacity: 0, height: 0 }}
                            className="text-sm text-red-600 dark:text-red-400 mt-1 flex items-center"
                          >
                            <AlertCircle className="h-4 w-4 inline mr-1 flex-shrink-0" />
                            {errors.description}
                          </motion.p>
                        )}
                      </AnimatePresence>
                      <p className="text-xs text-muted-foreground mt-1">
                        Clarity is key. Ensure voters understand the purpose and scope of the vote.
                      </p>
                    </div>
                  </CardContent>
                  <CardFooter className="flex justify-end bg-muted/30 border-t border-border/50 p-4">
                    <Button
                      type="button"
                      onClick={nextStep}
                      disabled={!validations.description}
                      className="min-w-[100px]"
                    >
                      Next Step
                    </Button>
                  </CardFooter>
                </Card>
              </motion.div>
            )}

            {/* Step 2: Configuration */}
            {currentStep === 2 && (
              <motion.div key="step2" {...fadeIn} transition={{ duration: 0.3 }}>
                <Card className="border border-border/50 shadow-sm overflow-hidden">
                  <CardHeader className="space-y-1 bg-muted/30 border-b border-border/50">
                    <Badge variant="secondary" className="w-fit mb-2">Step 2 of 4</Badge>
                    <CardTitle className="text-2xl font-semibold">Voting Configuration</CardTitle>
                    <CardDescription>
                      Define who can vote and how the results will be determined.
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="p-6 space-y-6">
                    <div className="space-y-3">
                      <Label className="text-sm font-medium flex items-center">
                          Voting Restriction
                          <TooltipProvider delayDuration={100}>
                            <Tooltip>
                              <TooltipTrigger asChild>
                                <Info className="h-4 w-4 ml-1.5 text-muted-foreground cursor-help" />
                              </TooltipTrigger>
                              <TooltipContent className="max-w-xs" side="right">
                                <p><strong>Open to All:</strong> Any address can vote. Simple and public.</p>
                                <p><strong>Limited Number:</strong> Only the first 'X' voters can participate. Closes after limit is reached.</p>
                                <p><strong>Registration Required:</strong> Voters must register before the start time. Allows for controlled participation (e.g., allowlist).</p>
                              </TooltipContent>
                            </Tooltip>
                          </TooltipProvider>
                        </Label>
                      <Select
                          value={formData.restriction}
                          onValueChange={(value) => handleSelectChange("restriction", value)}
                        >
                          <SelectTrigger className="w-full">
                            <SelectValue placeholder="Select voting restriction" />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="0">Open to All</SelectItem>
                            <SelectItem value="1">Limited Number of Voters</SelectItem>
                            <SelectItem value="2">Registration Required</SelectItem>
                          </SelectContent>
                      </Select>
                    </div>

                    {/* Conditional Max Voters Input */}
                    <AnimatePresence>
                      {(formData.restriction === "1" || formData.restriction === "2") && (
                        <motion.div
                          initial={{ opacity: 0, height: 0, marginTop: 0 }}
                          animate={{ opacity: 1, height: "auto", marginTop: '1.5rem' }} // Adjust spacing with marginTop
                          exit={{ opacity: 0, height: 0, marginTop: 0 }}
                          transition={{ duration: 0.2 }}
                          className="space-y-2 overflow-hidden" // Prevents content jump on exit
                        >
                          <div className="flex justify-between items-center">
                            <Label htmlFor="maxVoters" className="text-sm font-medium">
                              {formData.restriction === "1" ? "Max Voters Limit" : "Max Registrations (0 for unlimited)"}
                            </Label>
                             {validations.maxVoters && !errors.maxVoters && (
                                <motion.span
                                    initial={{ opacity: 0, scale: 0.5 }} animate={{ opacity: 1, scale: 1 }}
                                    className="text-xs text-green-600 dark:text-green-400 flex items-center">
                                    <CheckCircle2 className="h-3 w-3 mr-1" /> Valid
                                </motion.span>
                             )}
                          </div>
                          <Input
                            id="maxVoters"
                            name="maxVoters"
                            type="number"
                            min={formData.restriction === "1" ? "1" : "0"} // Min 1 for Limited, Min 0 for Registration
                            step="1"
                            placeholder={formData.restriction === "1" ? "Enter max voter count (e.g., 100)" : "Enter max registrations or 0 for unlimited"}
                            value={formData.maxVoters}
                            onChange={handleChange}
                            required={formData.restriction === "1"} // Only strictly required for limited type
                            className={`transition-all duration-150 ${
                              errors.maxVoters ? "border-red-500 focus-visible:ring-red-500/50 shadow-inner shadow-red-500/10" :
                              validations.maxVoters ? "border-green-500 focus-visible:ring-green-500/50 shadow-inner shadow-green-500/10" : "focus-visible:ring-primary/50"
                            }`}
                          />
                          <AnimatePresence>
                            {errors.maxVoters && (
                              <motion.p
                                initial={{ opacity: 0, height: 0 }}
                                animate={{ opacity: 1, height: "auto" }}
                                exit={{ opacity: 0, height: 0 }}
                                className="text-sm text-red-600 dark:text-red-400 mt-1 flex items-center"
                              >
                                <AlertCircle className="h-4 w-4 inline mr-1 flex-shrink-0" />
                                {errors.maxVoters}
                              </motion.p>
                            )}
                          </AnimatePresence>
                          <p className="text-xs text-muted-foreground">
                            {formData.restriction === "1"
                              ? "The campaign will close to new voters once this number is reached."
                              : "Set a limit on how many addresses can register. Use 0 for no registration limit."}
                          </p>
                        </motion.div>
                      )}
                    </AnimatePresence>

                    <div className="space-y-3 pt-2 border-t border-border/30 mt-6">
                       <Label className="text-sm font-medium flex items-center pt-4">
                         Result Type
                         <TooltipProvider delayDuration={100}>
                            <Tooltip>
                              <TooltipTrigger asChild>
                                <Info className="h-4 w-4 ml-1.5 text-muted-foreground cursor-help" />
                              </TooltipTrigger>
                              <TooltipContent className="max-w-xs" side="right">
                                <p><strong>Rank Based:</strong> Shows all proposals ordered by their vote count. Good for understanding overall preference.</p>
                                <p><strong>One Winner:</strong> Declares only the single proposal with the highest vote count as the winner. Ties may result in no winner depending on contract logic.</p>
                              </TooltipContent>
                            </Tooltip>
                          </TooltipProvider>
                       </Label>
                      <RadioGroup
                          value={formData.resultType}
                          onValueChange={(value) => handleSelectChange("resultType", value)}
                          className="flex flex-col sm:flex-row sm:space-x-4 space-y-2 sm:space-y-0"
                        >
                          <div className="flex items-center space-x-2 p-3 border rounded-md hover:bg-muted/50 transition-colors">
                            <RadioGroupItem value="0" id="rank-based" />
                            <Label htmlFor="rank-based" className="cursor-pointer font-normal">Rank Based</Label>
                          </div>
                          <div className="flex items-center space-x-2 p-3 border rounded-md hover:bg-muted/50 transition-colors">
                            <RadioGroupItem value="1" id="one-winner" />
                            <Label htmlFor="one-winner" className="cursor-pointer font-normal">One Winner</Label>
                          </div>
                      </RadioGroup>
                    </div>
                  </CardContent>
                  <CardFooter className="flex justify-between bg-muted/30 border-t border-border/50 p-4">
                    <Button
                      type="button"
                      variant="outline"
                      onClick={prevStep}
                      className="min-w-[100px]"
                    >
                      Previous
                    </Button>
                    <Button
                      type="button"
                      onClick={nextStep}
                      disabled={!validations.maxVoters}
                      className="min-w-[100px]"
                    >
                      Next Step
                    </Button>
                  </CardFooter>
                </Card>
              </motion.div>
            )}

            {/* Step 3: Time Frame */}
            {currentStep === 3 && (
              <motion.div key="step3" {...fadeIn} transition={{ duration: 0.3 }}>
                <Card className="border border-border/50 shadow-sm overflow-hidden">
                   <CardHeader className="space-y-1 bg-muted/30 border-b border-border/50">
                    <Badge variant="secondary" className="w-fit mb-2">Step 3 of 4</Badge>
                    <CardTitle className="text-2xl font-semibold">Time Frame</CardTitle>
                    <CardDescription>
                      Specify the exact start and end date/time for the voting period.
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="p-6 space-y-6">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      {/* Start Time */}
                      <div className="space-y-2">
                        <div className="flex justify-between items-center">
                          <Label htmlFor="startTime" className="text-sm font-medium">
                            Start Time (Your Local Time)
                          </Label>
                          {validations.startTime && !errors.startTime && (
                            <motion.span
                                initial={{ opacity: 0, scale: 0.5 }} animate={{ opacity: 1, scale: 1 }}
                                className="text-xs text-green-600 dark:text-green-400 flex items-center">
                                <CheckCircle2 className="h-3 w-3 mr-1" /> Valid
                            </motion.span>
                          )}
                        </div>
                        <Input
                          id="startTime"
                          name="startTime"
                          type="datetime-local"
                          min={minStartTimeStr}
                          value={formData.startTime}
                          onChange={handleChange}
                          required
                          className={`transition-all duration-150 ${
                            errors.startTime ? "border-red-500 focus-visible:ring-red-500/50 shadow-inner shadow-red-500/10" :
                            validations.startTime ? "border-green-500 focus-visible:ring-green-500/50 shadow-inner shadow-green-500/10" : "focus-visible:ring-primary/50"
                          }`}
                        />
                        <AnimatePresence>
                        {errors.startTime && (
                          <motion.p
                            initial={{ opacity: 0, height: 0 }}
                            animate={{ opacity: 1, height: "auto" }}
                            exit={{ opacity: 0, height: 0 }}
                            className="text-sm text-red-600 dark:text-red-400 mt-1 flex items-center"
                          >
                            <AlertCircle className="h-4 w-4 inline mr-1 flex-shrink-0" />
                            {errors.startTime}
                          </motion.p>
                        )}
                        </AnimatePresence>
                        <p className="text-xs text-muted-foreground">
                          Must be at least 3 minutes from now due to blockchain constraints.
                        </p>
                      </div>

                      {/* End Time */}
                      <div className="space-y-2">
                        <div className="flex justify-between items-center">
                          <Label htmlFor="endTime" className="text-sm font-medium">
                            End Time (Your Local Time)
                          </Label>
                          {validations.endTime && !errors.endTime && (
                            <motion.span
                                initial={{ opacity: 0, scale: 0.5 }} animate={{ opacity: 1, scale: 1 }}
                                className="text-xs text-green-600 dark:text-green-400 flex items-center">
                                <CheckCircle2 className="h-3 w-3 mr-1" /> Valid
                            </motion.span>
                          )}
                        </div>
                        <Input
                          id="endTime"
                          name="endTime"
                          type="datetime-local"
                          min={minEndTimeStr} // Dynamically set based on start time
                          value={formData.endTime}
                          onChange={handleChange}
                          required
                          disabled={!formData.startTime || !validations.startTime} // Disable until valid start time is entered
                          className={`transition-all duration-150 ${
                            errors.endTime ? "border-red-500 focus-visible:ring-red-500/50 shadow-inner shadow-red-500/10" :
                            validations.endTime ? "border-green-500 focus-visible:ring-green-500/50 shadow-inner shadow-green-500/10" : "focus-visible:ring-primary/50"
                          } ${(!formData.startTime || !validations.startTime) ? 'cursor-not-allowed opacity-60' : ''}`}
                        />
                         <AnimatePresence>
                            {errors.endTime && (
                              <motion.p
                                initial={{ opacity: 0, height: 0 }}
                                animate={{ opacity: 1, height: "auto" }}
                                exit={{ opacity: 0, height: 0 }}
                                className="text-sm text-red-600 dark:text-red-400 mt-1 flex items-center"
                              >
                                <AlertCircle className="h-4 w-4 inline mr-1 flex-shrink-0" />
                                {errors.endTime}
                              </motion.p>
                            )}
                         </AnimatePresence>
                        <p className="text-xs text-muted-foreground">
                          Must be set after the start time.
                        </p>
                      </div>
                    </div>

                    {/* Time Summary */}
                    {formData.startTime && formData.endTime && validations.startTime && validations.endTime && (
                      <motion.div
                        initial={{ opacity: 0, y: 10 }}
                        animate={{ opacity: 1, y: 0 }}
                        className="p-4 bg-primary/5 rounded-lg border border-primary/20 mt-6"
                      >
                        <h4 className="text-sm font-medium mb-3 text-primary/90">Voting Period Summary:</h4>
                        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 text-sm">
                          <div>
                            <p className="text-xs text-muted-foreground font-medium">STARTS</p>
                            <p className="font-medium">{new Date(formData.startTime).toLocaleString(undefined, { dateStyle: 'medium', timeStyle: 'short' })}</p>
                          </div>
                          <div>
                            <p className="text-xs text-muted-foreground font-medium">ENDS</p>
                            <p className="font-medium">{new Date(formData.endTime).toLocaleString(undefined, { dateStyle: 'medium', timeStyle: 'short' })}</p>
                          </div>
                        </div>
                      </motion.div>
                    )}
                  </CardContent>
                  <CardFooter className="flex justify-between bg-muted/30 border-t border-border/50 p-4">
                    <Button
                      type="button"
                      variant="outline"
                      onClick={prevStep}
                      className="min-w-[100px]"
                    >
                      Previous
                    </Button>
                    <Button
                      type="button"
                      onClick={nextStep}
                      disabled={!validations.startTime || !validations.endTime}
                      className="min-w-[100px]"
                    >
                      Next Step
                    </Button>
                  </CardFooter>
                </Card>
              </motion.div>
            )}

            {/* Step 4: Proposals */}
            {currentStep === 4 && (
              <motion.div key="step4" {...fadeIn} transition={{ duration: 0.3 }}>
                <Card className="border border-border/50 shadow-sm overflow-hidden">
                   <CardHeader className="space-y-1 bg-muted/30 border-b border-border/50">
                    <Badge variant="secondary" className="w-fit mb-2">Step 4 of 4</Badge>
                    <CardTitle className="text-2xl font-semibold">Proposals</CardTitle>
                    <CardDescription>
                      Define the specific options voters can choose from. Minimum of 2 required.
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="p-6 space-y-4">
                    <div className="flex justify-between items-center flex-wrap gap-2">
                      <div className="flex items-center">
                        <h3 className="text-sm font-medium">Proposal Options</h3>
                         {validations.proposals && !errors.proposals && (
                            <motion.span
                                initial={{ opacity: 0, scale: 0.5 }} animate={{ opacity: 1, scale: 1 }}
                                className="text-xs text-green-600 dark:text-green-400 flex items-center ml-2">
                                <CheckCircle2 className="h-3 w-3 mr-1" /> Valid
                            </motion.span>
                         )}
                      </div>
                      <Button
                        type="button"
                        variant="outline"
                        size="sm"
                        onClick={addProposal}
                        className="group"
                      >
                        <motion.div
                          whileHover={{ rotate: 90 }}
                          transition={{ type: "spring", stiffness: 400, damping: 15, duration: 0.2 }}
                          className="mr-2 flex items-center justify-center" // Ensure icon is centered
                        >
                          <Plus className="h-4 w-4" />
                        </motion.div>
                        Add Option
                      </Button>
                    </div>

                    <div className="space-y-3">
                      <AnimatePresence>
                        {formData.proposals.map((proposal, index) => (
                          <motion.div
                            key={`proposal-${index}`} // Use a more stable key if proposals can be reordered
                            className="flex items-center gap-2"
                            initial={{ opacity: 0, y: -10 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, x: -20, height: 0 }}
                            transition={{ duration: 0.2, type: "spring", stiffness: 300, damping: 20 }}
                          >
                            <Label htmlFor={`proposal-${index}`} className="text-sm font-medium text-muted-foreground w-8 text-right">{index + 1}.</Label>
                            <Input
                              id={`proposal-${index}`}
                              placeholder={`Enter proposal option ${index + 1}`}
                              value={proposal}
                              onChange={(e) => handleProposalChange(index, e.target.value)}
                              required
                              className={`flex-1 ${proposal.trim() === "" && errors.proposals ? 'border-red-300' : ''}`} // Highlight empty ones if overall error exists
                            />
                            <TooltipProvider delayDuration={100}>
                                <Tooltip>
                                <TooltipTrigger asChild>
                                    <Button
                                        type="button"
                                        variant="ghost"
                                        size="icon"
                                        onClick={() => removeProposal(index)}
                                        disabled={formData.proposals.length <= 2 || isSubmitting}
                                        className={`text-muted-foreground hover:text-destructive disabled:opacity-50 disabled:cursor-not-allowed ${formData.proposals.length <= 2 ? 'opacity-50 cursor-not-allowed' : ''}`}
                                        aria-label={`Remove proposal ${index + 1}`}
                                    >
                                        <Trash2 className="h-4 w-4" />
                                    </Button>
                                </TooltipTrigger>
                                {formData.proposals.length > 2 && <TooltipContent>Remove Option</TooltipContent>}
                                {formData.proposals.length <= 2 && <TooltipContent>Minimum 2 proposals required</TooltipContent>}
                                </Tooltip>
                            </TooltipProvider>
                          </motion.div>
                        ))}
                      </AnimatePresence>
                    </div>

                    <AnimatePresence>
                    {errors.proposals && (
                      <motion.p
                        initial={{ opacity: 0, height: 0 }}
                        animate={{ opacity: 1, height: "auto" }}
                        exit={{ opacity: 0, height: 0 }}
                        className="text-sm text-red-600 dark:text-red-400 mt-1 flex items-center"
                      >
                        <AlertCircle className="h-4 w-4 inline mr-1 flex-shrink-0" />
                        {errors.proposals}
                      </motion.p>
                    )}
                    </AnimatePresence>
                  </CardContent>
                   <CardFooter className="flex justify-between bg-muted/30 border-t border-border/50 p-4">
                    <Button
                      type="button"
                      variant="outline"
                      onClick={prevStep}
                      disabled={isSubmitting}
                      className="min-w-[100px]"
                    >
                      Previous
                    </Button>
                    <Button
                      type="submit"
                      disabled={!isFormValid() || isSubmitting || !contract}
                      className="min-w-[150px]"
                    >
                      {isSubmitting ? (
                         <>
                          <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                          Creating...
                         </>
                      ) : "Create Campaign"}
                    </Button>
                  </CardFooter>
                </Card>
              </motion.div>
            )}
          </AnimatePresence>
        </form>
      </div>
    </motion.div>
  );
}